.hyp-section {
  margin: 0 1%;
  min-height: 20px;
  display: block !important;
  position: relative;
  background: #f5f5f5;
  border: 1px dashed rgba(170, 170, 170, .9);
}
.hyp-section:first-child {
  margin-top: 45px;
}
.hyp-section .vc-container {
  width: 85%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 0;
  margin-bottom: 20px;
}
.hyp-section .hyper-container {
  width: 85%;
  margin: 5px auto;
}
