.showControl {
  border-color: #007bff !important;
}
.showControl>.vc-action-bar:first-child {
  visibility: visible;
}
.left-align {
  float: left;
}
.right-align {
  float: right;
}
.vc-action-bar {
  position: absolute;
  visibility: hidden;
  width: 200px;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: #007bff;
  clear: both;
  height: 20px;
  z-index: 5000;
}
.vc-action-bar .hyp-btn-wrap {
  line-height: 0;
  padding: 2px 5px;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
}
.vc-action-bar b-btn {
  line-height: 0;
  background: none;
  border: none;
  margin-right: 5px;
}
.vc-action-bar b-btn i {
  display: inline-grid;
  vertical-align: middle;
}
.vc-action-bar b-btn i svg {
  width: 13px;
  height: 13px;
  color: #fff;
}
.vc-action-bar b-btn:hover {
  background: #007bff;
  color: #fff;
  cursor: pointer;
}
.vc-action-bar span.editing {
  padding: 2px;
  color: #007bff;
}
.vc-action-bar span.editing svg {
  width: 12px;
  height: 12px;
}
.vc-action-bar span.itemType {
  font-size: 12px;
  line-height: 16px;
  color: #eee;
}
.vc-action-bar.forceVisible {
  visibility: visible !important;
  background: #aaa;
}
.vc-action-bar.editing {
  visibility: visible !important;
  background: #007bff;
}
