.addBlocksButtonWrapper {
  text-align: center;
  margin-bottom: 8px;
}
.addBlocksButtonWrapper b-btn {
  -webkit-box-shadow: rgba(0, 0, 0, .12) 0 1px 4px -1px;
          box-shadow: rgba(0, 0, 0, .12) 0 1px 4px -1px;
  background-color: #fff;
  border-radius: 3px;
  overflow: hidden;
  margin: 0;
  padding: 5px 8px;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
}
.addBlocksButtonWrapper b-btn i {
  display: inline-grid;
  vertical-align: middle;
}
.addBlocksButtonWrapper b-btn i svg {
  width: 13px;
  height: 13px;
}
.addBlocksButtonWrapper b-btn:hover {
  background: #007bff;
  color: #fff;
  cursor: pointer;
}
p.noBlockMessage {
  font-size: 11px;
  font-style: italic;
  margin-bottom: 2px;
  text-align: center;
  color: #e1e0e0;
}
